.promptContainer {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.title {
  font-size: 3rem;
}

.textarea {
  margin: 5rem 0;
  background-color: #0c0e10;
  border: none;
  outline: none;
  color: white;
  padding: 1rem;
}

.generateButton {
  background-color: white;
  padding: 8px 1rem;
  outline: none;
  border: none;
  border-radius: 2rem;
  cursor: pointer;
  font-size: 1.1rem;
}

.generateButton:active {
  transform: translateY(3px);
}

.resultContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 1rem;
  min-height: 100vh;
}

.resultTextContainer {
  background-color: #0c0e10;
  padding: 1rem;
  margin: 3rem 0;
}

.resultTextContainer p {
  margin: 1rem 0;
}

.audioPlayer {
  margin: 3rem 0 0 0;
}

@media only screen and (min-width: 800px) {
  .textarea {
    height: 200px;
    width: 800px;
  }

  .resultTextContainer {
    padding: 1rem;
    margin: 3rem 10rem;
    border-radius: 0.2rem;
    font-size: 1.2rem;
  }
}
